<template>
  <div class="contact">
    <h1>
      <span>Contact</span>
      <small>Yusuke Yamada - Portfolio</small>
    </h1>
    <article class="thanks">
      <h2>{{ contact.thanks.title }}</h2>
      <div class="txt" v-html="contact.thanks.txt"></div>
    </article>
  </div>
</template>

<script>
import contact from "../../static/contact.json";
export default {
  name: "thanks",
  title: "Thanks",
  description: "ありがとうございました",
  data() {
    return {
      contact: contact
    };
  }
};
</script>

<!-- css -->
<style src="../assets/css/contact.css"></style>


