<template>
  <article class="list">
    <section v-for="(d,key) in data" :key="key">
      <p class="kind" v-cloak>{{ d.kind }}</p>
      <p class="icon" v-cloak>
        <router-link :to="'/works/' + d.slug">
          <img :src="d.icon">
        </router-link>
      </p>
      <div class="titleBg">
        <p class="title" v-cloak>
          <b v-html="d.title"></b>
        </p>
      </div>
      <p class="note">{{ d.note }}</p>
      <div class="role" v-cloak>
        <b>担当箇所：</b>
        <ul>
          <li v-for="(i,key) in d.role" :key="key">{{ i }}</li>
        </ul>
      </div>
    </section>
  </article>
</template>

<script>
export default {
  title: "Works",
  description: "山田祐介の過去の制作物一覧",
  props: ["data"]
};
</script>

<!-- css -->
<style src="../../assets/css/works.css"></style>


