<template>
  <article class="worksDetail">
    <h2 v-html="filteredData.urlTitle" v-cloak></h2>
    <p class="img" v-cloak>
      <img :src="filteredData.picture">
    </p>
    <ul class="list">
      <li v-for="(d,key) in filteredData.role" :key="key">{{ d }}</li>
    </ul>
    <p class="txt" v-html="filteredData.txt" v-cloak></p>
    <p class="url" v-cloak>
      URL：
      <a :href="filteredData.url" target="_blank">{{ filteredData.urlTitle }}</a>
    </p>
    <p class="btn">
      <a href="#" onclick="window.history.back(); return false;">← 一覧へ戻る</a>
    </p>
  </article>
</template>

<script>
export default {
  props: ["data"],
  computed: {
    filteredData: function() {
      var url =
        window.location.protocol + "//" + window.location.host + "/works/";
      var matchData = this.data.filter(function(item, index) {
        if (item.slug === window.location.href.replace(url, "")) return true;
      });
      return matchData[0];
    }
  },
  mounted() {
    document.title =
      this.filteredData.urlTitle + " | Yusuke Yamada - Portfolio";
    let meta = document.getElementsByTagName("meta");
    for (var i = 0; i < meta.length; i++) {
      if (meta[i].name.toLowerCase() == "description") {
        meta[i].content = this.filteredData.urlTitle + "のページです";
      }
    }
  }
};
</script>

<!-- css -->
<style src="../../assets/css/works.css"></style>


