<template>
  <div class="about">
    <h1>
      <span>About</span>
      <small>Yusuke Yamada - Portfolio</small>
    </h1>
    <article class="person">
      <dl v-for="(d,key) in about.person" :key="key" v-cloak>
        <dt>{{ d.title }}</dt>
        <dd>{{ d.txt }}</dd>
      </dl>
    </article>
    <article class="career">
      <h2>{{ about.career.title }}</h2>
      <div class="list">
        <dl v-for="(d,key) in about.career.detail" :key="key" v-cloak>
          <dt>
            <h3>{{ d.company }}</h3>
            <small>{{ d.date }}</small>
          </dt>
          <dd>{{ d.txt }}</dd>
        </dl>
      </div>
    </article>
    <article class="skill">
      <h2>{{ about.skill.title }}</h2>
      <section>
        <div class="list" v-for="(d,key) in about.skill.detail" :key="key" v-cloak>
          <h3>{{ d.title }}</h3>
          <dl v-for="(i,key) in d.language" :key="key" v-cloak>
            <dt>{{ i.name }}</dt>
            <dd>{{ i.note }}</dd>
          </dl>
        </div>
      </section>
    </article>
    <article class="tool">
      <h2>{{ about.tool.title }}</h2>
      <ul class="list">
        <li v-for="(list,key) in about.tool.list" :key="key" v-cloak>{{ list }}</li>
      </ul>
    </article>
  </div>
</template>

<script>
import about from "../../static/about.json";
export default {
  name: "about",
  title: "About",
  description: "山田祐介について",
  data() {
    return {
      about: about
    };
  }
};
</script>

<!-- css -->
<style src="../assets/css/about.css"></style>


