<template>
  <div class="contact">
    <h1>
      <span>Contact</span>
      <small>Yusuke Yamada - Portfolio</small>
    </h1>
    <article class="form">
      <form
        action="https://script.google.com/macros/s/AKfycbzpkUuH2iCG0AMuMbrsvlsad5uQIU-IHPH6enRZ9syAupWn7ZnD/exec"
      >
        <input
          name="SPREADSHEET_ID"
          type="hidden"
          value="1u6tHwq-EiCpnpQkMPT_JH-TbYEYXrTppk9JXq_fbSpY"
        >
        <input name="SHEET_NAME" type="hidden" value="フォームデータ">

        <dl>
          <dt>メール</dt>
          <dd>
            <input type="email" name="your-email" placeholder="ご連絡不要であれば空欄">
          </dd>
        </dl>
        <dl>
          <dt>お名前</dt>
          <dd>
            <input type="text" name="your-name" placeholder="匿名希望の場合は空欄">
          </dd>
        </dl>
        <dl>
          <dt>
            メッセージ
            <i>必須</i>
          </dt>
          <dd>
            <textarea
              name="your-message"
              placeholder="この欄の記入は必須となっております。"
              rows="8"
              required
              id="message"
            ></textarea>
          </dd>
        </dl>
        <div class="btn">
          <button type="submit" v-on:click="sendMessage()">Submit</button>
        </div>
      </form>
    </article>
    <article class="detail">
      <div class="txt">
        <p v-html="contact.detail.txt"></p>
        <small v-html="contact.detail.note"></small>
      </div>
    </article>
  </div>
</template>

<script>
import contact from "../../static/contact.json";
export default {
  name: "contact",
  title: "Contact",
  description: "コンタクトフォーム",
  data() {
    return {
      contact: contact
    };
  },
  methods: {
    sendMessage() {
      var form = $("form");
      var submitBtn = form.find("button[type=submit]");
      if ($("#message").val() === "") {
        alert("メッセージを入力してください");
        return false;
      }
      $.ajax({
        url: form.attr("action"),
        dataType: "jsonp",
        data: form.serialize(),
        beforeSend: function() {
          return submitBtn.prop("disabled", true);
        },
        complete: function() {
          return submitBtn.prop("disabled", false);
        },
        success: function(response) {
          window.location.href = "/thanks";
        },
        error: function(response) {
          return console.log(response);
        }
      });
    }
  }
};
</script>

<!-- css -->
<style src="../assets/css/contact.css"></style>


