<template>
  <div class="top">
    <h1>
      <span>Yusuke Yamada - Portfolio</span>
    </h1>
    <article class="log">
      <h2 v-cloak>{{ top.update.title }}</h2>
      <div class="list">
        <dl v-for="(d,key) in top.update.log" :key="key" v-cloak>
          <dt>{{ d.date }}</dt>
          <dd>{{ d.matter }}</dd>
        </dl>
      </div>
    </article>
    <article class="whats">
      <h2 v-cloak>{{ top.whats.title }}</h2>
      <div class="txt" v-html="top.whats.txt"></div>
    </article>
  </div>
</template>

<script>
import top from "../../static/top.json";
export default {
  name: "top",
  title: "Top",
  description: "山田祐介のポートフォリオサイト",
  data() {
    return {
      top: top
    };
  }
};
</script>

<!-- css -->
<style src="../assets/css/top.css"></style>


