import Vue from 'vue';
import Router from 'vue-router';
import Top from '@/components/Top';
import Portfolio from '@/components/Portfolio';
import About from '@/components/About';
import Works from '@/components/Works';
import WorksTop from '@/components/parts/WorksTop';
import WorksDetail from '@/components/parts/WorksDetail';
import Contact from '@/components/Contact';
import Thanks from '@/components/Thanks';
import PageNotFound from '@/components/404';

Vue.use(Router);

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'top',
      component: Top
    },
    {
      path: '/portfolio',
      name: 'portfolio',
      component: Portfolio
    },
    {
      path: '/about',
      name: 'about',
      component: About
    },
    {
      path: '/works',
      component: Works,
      children: [
        {
          path: '/',
          name: 'WorksTop',
          component: WorksTop
        },
        {
          path: ':slug',
          name: 'WorksDetail',
          component: WorksDetail
        }
      ]
    },

    {
      path: '/contact',
      name: 'contact',
      component: Contact
    },
    {
      path: '/thanks',
      name: 'thanks',
      component: Thanks
    },
    {
      path: '*',
      name: 'Not Found',
      component: PageNotFound,
      title: 'Not Found'
    }
  ]
});



